<template>
<div class="padding-t_10" id="reportstabsdata">
    <div class="left-menu-tab new_tab">
        <div class="col-md-12 p-0">
            <div class="theme-primary partition">
                <span class="p-name text-white">{{ $t("ProductReport") }}</span>
            </div>
        </div>
        <div class="pt-2 mb-1 w-100 d-none d-lg-block">
            <div class="input-group customsearchbox">
                <input type="text" maxlength="50" :placeholder="Search" class="form-control searchcustomfilter border-right-0" autocomplete="off" v-model="searchQuery" />
                <div class="input-group-append">
                    <span class="input-group-text bg-transparent bg-white border-left-0"><i class="fa fa-search"></i></span>
                </div>
            </div>
        </div>
        <div class="mt-2" id="divscrollsetuptabs" style="max-height: 700px">
            <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix lefttabflexible" id="upper" role="tablist">
                <li v-for="(tab, index) in filteredTabs" :key="index">
                    <a class="nav-link left-menu" :data-toggle="'#' + tab.id" :class="{ active: activeKey === tab.index }" @click="activateTab(tab.index)">
                        <i :class="tab.icon" aria-hidden="true"></i>{{ $t(tab.name) }}
                    </a>
                </li>
            </ul>
        </div>
    </div>

    <!-- right-content here -->
    <div class="right-content">
        <loader :isVisible="isLoading"></loader>

        <div class="col-md-12 p-0">
            <div class="theme-primary partition" style="overflow: visible">
                <span class="p-name text-white" id="spnreporttext">{{
            this.$t(activeTabName)
          }}</span>

                <!-- Harjit Singh 12March 2024 - There is no need of Action button, as Live App was also have wrong Action button. -->
                <!-- <span class="p-actions float-right">
            <a
              href="javascript:;" id="ancshowmorefilter" class="p-action-btn text-white"
              style="display: none"  :title="$t('Show') + ' ' + $t('More') + ' ' + $t('Filter')">
              <i class="fa fa-filter" aria-hidden="true"></i><br />{{$t("Show")}}
              {{ $t("More") }} {{ $t("Filter") }}</a>
          </span>
          <span id="showDaterange" style="display: none" class="p-actions float-right p-2">
            @await Html.PartialAsync("~/Areas/BiReports/Views/Shared/DateRange.cshtml")
          </span>
          <span class="collapse-head-btns">
            <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
          </span> -->
                <!-- Harjit Singh 12March 2024 - There is no need of Action button, as Live App was also have wrong Action button. -->
            </div>
        </div>
        <div class="col-md-12 p-0">
            <div class="tab-content" id="myTabContent">
                <div class="expand-list">
                    <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                        <i class="icon-solid arrow-solid-left text-white" style="font-size: 9pt"></i>
                    </a>
                </div>
                <div id="ProductAssignmentReport" class="tab-pane" :class="{ 'active show': activeKey == 0 }" v-if="activeKey == 0">
                    <ProductAssignmentReports></ProductAssignmentReports>
                </div>
                <div id="AuditLogReport" class="tab-pane" :class="{ 'active show': activeKey == 1 }" v-if="activeKey == 1">
                    <AuditLogReport></AuditLogReport>
                </div>
                <div id="ProductBarcodeReport" class="tab-pane" :class="{ 'active show': activeKey == 2 }" v-if="activeKey == 2">
                    <ProductBarcodeReport></ProductBarcodeReport>
                </div>
                <div id="ItemLogHistoryReport" class="tab-pane" :class="{ 'active show': activeKey == 3 }" v-if="activeKey == 3">
                    <ItemLogHistoryReport></ItemLogHistoryReport>
                </div>
                <div id="ProductCountReport" class="tab-pane" :class="{ 'active show': activeKey == 4 }" v-if="activeKey == 4">
                    <ProductCountReport></ProductCountReport>
                </div>
                <div id="ProductReport" class="tab-pane" :class="{ 'active show': activeKey == 5 }" v-if="activeKey == 5">
                    <ProductReport></ProductReport>
                </div>
            </div>
        </div>
    </div>
    <!-- right-content End here -->
</div>
</template>

<script>

import '@boldreports/javascript-reporting-controls/Scripts/bold.report-designer.min';
import '@boldreports/javascript-reporting-controls/Scripts/bold.report-viewer.min';

//Data-Visualization
import '@boldreports/javascript-reporting-controls/Scripts/data-visualization/ej.bulletgraph.min';
import '@boldreports/javascript-reporting-controls/Scripts/data-visualization/ej.chart.min';

import ProductAssignmentReports from "../AssetReport/ProductAssignmentReports.vue";
import AuditLogReport from "../AssetReport/AuditLogReport.vue";
import ProductBarcodeReport from "../AssetReport/ProductBarcodeReport.vue";
import ItemLogHistoryReport from "../AssetReport/ItemLogHistoryReport.vue";
import ProductCountReport from "../AssetReport/ProductCountReport.vue";
import ProductReport from "../AssetReport/ProductReport.vue";


export default {
    components: {
        ProductAssignmentReports,
        AuditLogReport,
        ProductBarcodeReport,
        ItemLogHistoryReport,
        ProductCountReport,
        ProductReport,
    },
    data() {
        return {
            activeKey: 0,
            tabNames: [
                "ProductAssignmentReport",
                "AuditLogReport",
                "ProductBarcodeReport",
                "ItemLogHistoryReport",
                "ProductCountReport",
                "ProductReport"
            ],
            searchQuery: "",
        };
    },
    mounted() {
        this.activateTab(0);
    },
    computed: {
    filteredTabs() {
        if (!this.searchQuery) {           
            return this.tabNames.map((name, index) => ({
                name,
                index,
                icon: this.getTabIcon(name)
            }));
        }
        const query = this.searchQuery.toLowerCase();
        const filtered = this.tabNames
            .map((name, index) => ({
                name,
                index,
                icon: this.getTabIcon(name)
            }))
            .filter(tab => this.$t(tab.name).toLowerCase().includes(query));

        if (query.trim() !== '' && filtered.length === 0) {           
            return [{ name: 'No tabs found for "' + this.searchQuery + '"', index: -1, icon: 'no-icon' }];
        }

        return filtered;
    },
    activeTabName() {
        return this.tabNames[this.activeKey]; // Returns the name of the currently active tab
    }
},

    methods: {
        getTabIcon(name) {
            switch (name) {
                case "ProductAssignmentReport":
                    return "fa fa-list";
                case "AuditLogReport":
                    return "fa fa-list-alt";
                case "ProductBarcodeReport":
                    return "fa fa-money";
                case "ItemLogHistoryReport":
                    return "fa fa-file-text";
                case "ProductCountReport":
                    return "fa fa-file-text";
                case "ProductReport":
                    return "fa fa-file-text";
                default:
                    return "";
            }
        },
        activateTab(index) {
            this.activeKey = index;
        },
    },
};
</script>
